.block {
  display: flex;
  margin-bottom: 30px;
}

.block-title {
  min-width: 20%;
}

.block-title h4 {
  margin: 0;
}

.language-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.language-item {
  padding: 5px 10px;
}

.public-block {
  padding: 0 20px;
  min-width: 80%;
}